<template>
  <div class="col-12">
    <b-card class="bg-white rounded mb-1" bg-variant="dark" text-variant="dark" no-body>
      <div slot="header" class="text-light">
        <strong>Store</strong> Displays
        <b-button id="btn16" type="submit" size="sm" variant="success" class="pull-right" v-b-modal.displayAdd>
          <i class="fa fa-plus"></i> Add
        </b-button>
        <a href="https://docs.range-pro.com/store_account.html#displays" target="new" class="button">
          <b-button id="btn17" type="submit" size="sm" variant="primary" class="pull-right mr-1"><i class="fa fa-question-circle"></i> Help</b-button>
        </a>
      </div>
    </b-card>


    <b-card v-if="loading" class="bg-white rounded text-center text-primary my-2" bg-variant="dark">
      <b-spinner class="align-middle"></b-spinner>
      <strong>Loading...</strong>
    </b-card>


    <b-card
      v-if="!loading"
      v-for="display in item_list"
      class="bg-white rounded mb-1"
      bg-variant="dark">
      <b-row class="p-3">
        <b-col cols="8" md="9" class="pl-3 pr-5">
          <b-row>
            <h2 class="text-gray-700"><i class="fa fa-tv mr-1 text-danger"></i> {{ trimHeading(display.name).toUpperCase() }}</h2>
            <medium class="text-primary text-sm-left mt-2 ml-2 mb-2">{{ display.mode.toUpperCase() }}</medium>
          </b-row>
          <b-row>
            <b-col cols="12" md="2">
              <strong>Link:</strong>
            </b-col>
            <b-col cols="12" md="10">
              <a v-bind:href="'http://display.range-pro.com/#/code/' + display.code" target="_blank"><i class="fa fa-external-link mr-1"></i> http://display.range-pro.com/#/code/{{display.code}} </a>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" md="3">
              <strong>Range:</strong>
            </b-col>
            <b-col cols="12" md="3">
              {{ getRangeName(display.rangeID).toUpperCase() }}
            </b-col>
            <b-col cols="12" md="3">
              <strong>Code:</strong>
            </b-col>
            <b-col cols="12" md="3">
              {{ display.code }}
            </b-col>
          </b-row>
        </b-col>
        <b-col cols="4" md="3">
            <b-row class="mb-1">
              <b-button block  id="btn18" type="submit" size="sm" variant="warning" @click="selectDisplay(display.name, display.mode, display.rangeID, display.id)"><i class="fa fa-trash-o"></i> Edit</b-button>
            </b-row>
          <b-row class="mb-1">
            <b-button block id="btn19" type="submit" size="sm" variant="danger" @click="confirmDelete(display.id)"><i class="fa fa-trash-o"></i> Delete</b-button>
          </b-row>
        </b-col>
      </b-row>
    </b-card>


    <b-modal ref="confirmDelete" id="confirmDelete" header-bg-variant="dark" header-text-variant="light" centered hide-footer title="Delete Display">
      <div class="d-block text-left text-dark">
        <p>Please confirm to delete display.</p>
      </div>
      <b-btn id="btn305" class="mt-3" variant="outline-danger" block @click="deleteItem()">Confirm</b-btn>
    </b-modal>


    <b-modal ref="displayAdd" id="displayAdd" header-bg-variant="dark" header-text-variant="light" size="lg" centered hide-footer title="Add Display">
      <div ref="card" class="text-dark">

        <b-form-group
          label="Display Name"
          label-for="displayName"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="displayName" type="text" v-model="addDisplay.name"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.addDisplay.name.required">Display Name is Required</span>
        </b-form-group>

        <b-form-group
          label="Display Mode"
          label-for="displayMode"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="displayMode"
                         :plain="true"
                         :options="mode_options"
                         v-model="addDisplay.mode">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Range"
          label-for="displayRange"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="displayRange"
                         :plain="true"
                         :options="range_options"
                         v-model="addDisplay.rangeID">
          </b-form-select>
          <span class="form-group__message text-danger" v-if="!$v.addDisplay.rangeID.required">Display Range is Required</span>
        </b-form-group>

        <b-btn id="btn306" class="mt-3" variant="outline-success" block @click="saveDisplay()" :disabled="$v.addDisplay.$invalid">Add Display</b-btn>

      </div>
    </b-modal>

    <b-modal ref="displayEdit" id="displayEdit" header-bg-variant="dark" header-text-variant="light" size="lg" centered hide-footer title="Edit Display">
      <div ref="card" class="text-dark">

        <b-form-group
          label="Display Name"
          label-for="displayNameEdit"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="displayNameEdit" type="text" v-model="editDisplay.name"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.editDisplay.name.required">Display Name is Required</span>
        </b-form-group>

        <b-form-group
          label="Display Mode"
          label-for="displayModeEdit"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="displayModeEdit"
                         :plain="true"
                         :options="mode_options"
                         v-model="editDisplay.mode">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Range"
          label-for="displayRangeEdit"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="displayRangeEdit"
                         :plain="true"
                         :options="range_options"
                         v-model="editDisplay.rangeID">
          </b-form-select>
          <span class="form-group__message text-danger" v-if="!$v.editDisplay.rangeID.required">Display Range is Required</span>
        </b-form-group>

        <b-btn id="btn307" class="mt-3" variant="outline-success" block @click="updateDisplay()" :disabled="$v.editDisplay.$invalid">Save Display</b-btn>

      </div>
    </b-modal>

  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import formats from '@/services/formats'
  import { required } from 'vuelidate/lib/validators'

  export default {
    name: 'StoreDisplays',
    data () {
      return {
        item_list: [],
        ranges: [],
        range_options: [],
        mode_options: [
          {text: 'RANGE DISPLAY ONLY', value: 'range only'},
          {text: 'WAITING LIST ONLY', value: 'waiting list only'},
          {text: 'RANGE & WAITING LIST CYCLE', value: 'cycle range and waiting list'}
        ],
        delete_id: null,
        loading: true,
        edit_id: null,
        editDisplay: {
          'name': null,
          'mode': 'range only',
          'rangeID': null
        },
        addDisplay: {
          'name': null,
          'mode': 'range only',
          'rangeID': null
        }
      }
    },
    mounted: function () {
      this.getData()
      this.getStoreRanges()
    },
    methods: {
      getData (response) {
        rangepro.get_secure('store/displays', this.handleResponse, 'GetStoreDisplay')
      },
      getStoreRanges (response) {
        rangepro.get_secure('store/ranges', this.handleRangesResponse, 'GetStoreRanges')
      },
      saveDisplay () {
        this.loading = true
        var display = {
          display: {
            name: this.addDisplay.name,
            mode: this.addDisplay.mode,
            rangeID: this.addDisplay.rangeID
          }
        }
        rangepro.post_secure('store/displays', display, this.handleAddDisplaysResponse, 'AddStoreDisplay', 'Successfully Added Display')
      },
      updateDisplay () {
        this.loading = true
        var display = {
          display: {
            name: this.editDisplay.name,
            mode: this.editDisplay.mode,
            rangeID: this.editDisplay.rangeID
          }
        }
        rangepro.put_secure('store/display/' + this.edit_id, display, this.handleAddDisplaysResponse, 'UpdateStoreDisplay', 'Successfully Added Display')
      },
      deleteItem () {
        this.loading = true
        this.$refs.confirmDelete.hide()
        rangepro.delete_secure('store/display', this.delete_id, this.getData, 'DeleteStoreDisplay', 'Removed Store Display')
      },
      confirmDelete (id) {
        this.$refs.confirmDelete.show()
        this.delete_id = id
      },
      handleAddDisplaysResponse (data, success) {
        if (success) {
          this.addDisplay.name = null
          this.addDisplay.mode = 'range only'
          this.addDisplay.rangeID = null
          this.editDisplay.name = null
          this.editDisplay.mode = 'range only'
          this.editDisplay.rangeID = null
          this.edit_id = null
          this.$refs.displayAdd.hide()
          this.$refs.displayEdit.hide()
          this.getData()
        } else {
          this.loading = false
        }
      },
      handleResponse (data, success) {
        if (success) {
          this.item_list = data.displays
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleRangesResponse (data, success) {
        if (success) {
          this.ranges = data.ranges
          this.range_options = []
          for (var index in this.ranges) {
            this.range_options.push({
              text: this.ranges[index]['name'],
              value: this.ranges[index]['id']
            })
          }
          this.loading = false
        } else {
          this.loading = false
        }
      },
      selectDisplay (name, mode, rangeID, id) {
        this.edit_id = id
        this.editDisplay.name = name
        this.editDisplay.mode = mode
        this.editDisplay.rangeID = rangeID
        this.$refs.displayEdit.show()
      },
      getRangeName (id) {
        for (var index in this.ranges) {
          if (this.ranges[index]['id'] === id) {
            return this.ranges[index]['name']
          }
        }
        return 'RANGE DELETED'
      },
      getRowCount (items) {
        return items.length
      },
      getBadge (status) {
        return formats.getBadge(status)
      },
      getYesNo (value) {
        return formats.format_yes_no(value)
      },
      isMobile () {
        if (screen.width <= 760) {
          return true
        } else {
          return false
        }
      },
      trimHeading (value) {
        if (value.length > 5 && this.isMobile()) {
          return value.substring(0, 8) + '..'
        }
        return value
      }
    },
    validations: {
      addDisplay: {
        name: {
          required
        },
        rangeID: {
          required
        }
      },
      editDisplay: {
        name: {
          required
        },
        rangeID: {
          required
        }
      }
    }
  }
</script>

<style scoped>

</style>



