<template>
  <div class="col-12">
    <b-card class="bg-white rounded mb-1" bg-variant="dark" text-variant="dark" no-body>
      <div slot="header" class="text-light">
        <strong>Store</strong> Kiosks
        <b-button id="btn62" type="submit" size="sm" variant="success" class="pull-right" v-b-modal.kioskAdd>
          <i class="fa fa-plus"></i> Add
        </b-button>
        <a href="https://docs.range-pro.com/store_account.html#kiosks" target="new" class="button">
          <b-button id="btn63" type="submit" size="sm" variant="primary" class="pull-right mr-1"><i class="fa fa-question-circle"></i> Help</b-button>
        </a>
      </div>
    </b-card>


    <b-card v-if="loading" class="bg-white rounded text-center text-primary my-2" bg-variant="dark">
      <b-spinner class="align-middle"></b-spinner>
      <strong>Loading...</strong>
    </b-card>


    <b-card
      v-if="!loading"
      v-for="kiosk in item_list"
      class="bg-white rounded mb-1"
      bg-variant="dark">
      <b-row class="p-3">
        <b-col cols="8" md="9" class="pl-3 pr-5">
          <b-row>
            <h2 class="text-gray-700"><i class="fa fa-tablet mr-1 text-danger"></i> {{ trimHeading(kiosk.name).toUpperCase() }}</h2>
          </b-row>
          <b-row>
            <b-col cols="8" md="3">
              <strong>Waivers:</strong>
            </b-col>
            <b-col cols="4" md="3">
              <b-badge pill class="ml-2 w-100" :variant="getBadge(kiosk.waivers)">{{ getYesNo(kiosk.waivers) }}</b-badge>
            </b-col>
            <b-col cols="8" md="3">
              <strong>Waiting List:</strong>
            </b-col>
            <b-col cols="4" md="3">
              <b-badge pill class="ml-2 w-100" :variant="getBadge(kiosk.waitinglist)">{{ getYesNo(kiosk.waitinglist) }}</b-badge>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="8" md="3">
              <strong>Self-Service:</strong>
            </b-col>
            <b-col cols="4" md="3">
              <b-badge pill class="ml-2 w-100" :variant="getBadge(kiosk.selfservice)">{{ getYesNo(kiosk.selfservice) }}</b-badge>
            </b-col>
            <b-col cols="8" md="3">

            </b-col>
            <b-col cols="4" md="3">

            </b-col>
          </b-row>
        </b-col>
        <b-col cols="4" md="3">
          <router-link :to="{ name: 'Kiosk Dashboard', params: { id:  kiosk.id}}" target="_blank" class="text-decoration-none">
            <b-row class="mb-1">
              <b-button block id="btn64" type="submit" size="sm" variant="secondary"><i class="fa fa-th-large"></i> Dashboard</b-button>
            </b-row>
          </router-link>
          <b-row class="mb-1">
            <b-button block id="btn65" type="submit" size="sm" variant="warning" @click="selectKiosk(kiosk.name, kiosk.waivers, kiosk.waitinglist, kiosk.selfservice, kiosk.ss_assign_text, kiosk.id )"><i class="fa fa-trash-o"></i> Edit</b-button>
          </b-row>
          <b-row class="mb-1">
            <b-button block id="btn66" type="submit" size="sm" variant="danger" @click="confirmDelete(kiosk.id)"><i class="fa fa-trash-o"></i> Delete</b-button>
          </b-row>
        </b-col>
      </b-row>
    </b-card>

    <b-modal ref="confirmDelete" header-bg-variant="dark" header-text-variant="light" centered hide-footer title="Delete Kiosk">
      <div class="d-block text-left text-dark">
        <p>Please confirm to delete kiosk.</p>
      </div>
      <b-btn id="btn331" class="mt-3" variant="outline-danger" block @click="deleteItem()">Confirm</b-btn>
    </b-modal>


    <b-modal ref="kioskAdd" id="kioskAdd" header-bg-variant="dark" header-text-variant="light" size="lg" centered hide-footer title="Add Kiosk">
      <div ref="card" class="text-dark">

        <b-form-group
          label="Display Name"
          label-for="displayName"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="displayName" type="text" v-model="addKiosk.name"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.addKiosk.name.required">Kiosk Name is Required</span>
        </b-form-group>

        <b-form-group
          label="Waivers"
          label-for="waiverEnabled"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="waiverEnabled"
                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="addKiosk.waivers">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Waiting List"
          label-for="wlEnabled"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="wlEnabled"
                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="addKiosk.waitinglist">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Self-Service"
          label-for="ssEnabled"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="ssEnabled"
                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="addKiosk.selfservice">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Self-Service Assignment Text"
          label-for="ssAssignText"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="ssAssignText" type="text" v-model="addKiosk.ss_assign_text"></b-form-input>
        </b-form-group>

        <b-btn id="btn332" class="mt-3" variant="outline-success" block @click="saveKiosk()" :disabled="$v.addKiosk.$invalid">Add Kiosk</b-btn>

      </div>
    </b-modal>

    <b-modal ref="kioskEdit" id="kioskEdit" header-bg-variant="dark" header-text-variant="light" size="lg" centered hide-footer title="Edit Kiosk">
      <div ref="card" class="text-dark">

        <b-form-group
          label="Display Name"
          label-for="displayName"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="displayName" type="text" v-model="editKiosk.name"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.editKiosk.name.required">Kiosk Name is Required</span>
        </b-form-group>

        <b-form-group
          label="Waivers"
          label-for="waiverEnabled"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="waiverEnabled"
                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="editKiosk.waivers">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Waiting List"
          label-for="wlEnabled"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="wlEnabled"
                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="editKiosk.waitinglist">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Self-Service"
          label-for="ssEnabled"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="ssEnabled"
                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="editKiosk.selfservice">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Self-Service Assignment Text"
          label-for="ssAssignText"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="ssAssignText" type="text" v-model="editKiosk.ss_assign_text"></b-form-input>
        </b-form-group>

        <b-btn id="btn333" class="mt-3" variant="outline-success" block @click="updateKiosk()" :disabled="$v.editKiosk.$invalid">Save Kiosk</b-btn>

      </div>
    </b-modal>

  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import formats from '@/services/formats'
  import { required } from 'vuelidate/lib/validators'

  export default {
    name: 'StoreKiosks',
    data () {
      return {
        item_list: [],
        delete_id: null,
        loading: true,
        currentPage: 1,
        perPage: 5,
        totalRows: 0,
        edit_id: null,
        editKiosk: {
          'name': null,
          'waivers': false,
          'waitinglist': false,
          'selfservice': false,
          'ss_assign_text': 'None'
        },
        addKiosk: {
          'name': null,
          'waivers': false,
          'waitinglist': false,
          'selfservice': false,
          'ss_assign_text': 'None'
        },
        fields: [
          {
            key: 'name',
            label: 'Display Name',
            sortable: true
          },
          {
            key: 'waivers',
            label: 'Waivers',
            formatter: (value) => formats.format_yes_no(value)
          },
          {
            key: 'waitinglist',
            label: 'Waiting List',
            formatter: (value) => formats.format_yes_no(value)
          },
          {
            key: 'selfservice',
            label: 'Self-Service',
            formatter: (value) => formats.format_yes_no(value)
          },
          {
            key: 'id',
            label: 'Action'
          }
        ]
      }
    },
    mounted: function () {
      this.getData()
    },
    methods: {
      getData (response) {
        rangepro.get_secure('store/kiosks', this.handleResponse, 'GetStoreKiosks')
      },
      saveKiosk () {
        this.loading = true
        var kiosk = {
          kiosk: this.addKiosk
        }
        rangepro.post_secure('store/kiosks', kiosk, this.handleAddKiosksResponse, 'AddStoreKiosk', 'Successfully Added Kiosk')
      },
      updateKiosk () {
        this.loading = true
        var kiosk = {
          kiosk: this.editKiosk
        }
        rangepro.put_secure('store/kiosk/' + this.edit_id, kiosk, this.handleAddKiosksResponse, 'UpdateStoreKiosk', 'Successfully Updated Kiosk')
      },
      deleteItem () {
        this.loading = true
        this.$refs.confirmDelete.hide()
        rangepro.delete_secure('store/kiosk', this.delete_id, this.getData, 'DeleteStoreKiosks', 'Removed Store Kiosk')
      },
      confirmDelete (id) {
        this.$refs.confirmDelete.show()
        this.delete_id = id
      },
      handleAddKiosksResponse (data, success) {
        if (success) {
          this.addKiosk.name = null
          this.addKiosk.waivers = false
          this.addKiosk.waitinglist = false
          this.addKiosk.selfservice = false
          this.addKiosk.ss_assign_text = null
          this.editKiosk.name = null
          this.editKiosk.waivers = false
          this.editKiosk.waitinglist = false
          this.editKiosk.selfservice = false
          this.editKiosk.ss_assign_text = null
          this.edit_id = null
          this.$refs.kioskAdd.hide()
          this.$refs.kioskEdit.hide()
          this.getData()
        }
        this.loading = false
      },
      handleResponse (data, success) {
        if (success) {
          this.item_list = data.kiosks
          this.loading = false
        } else {
          this.loading = false
        }
      },
      selectKiosk (name, waivers, waitinglist, selfservice, assignText, id) {
        this.edit_id = id
        this.editKiosk.name = name
        this.editKiosk.waivers = waivers
        this.editKiosk.waitinglist = waitinglist
        this.editKiosk.selfservice = selfservice
        this.editKiosk.ss_assign_text = assignText
        this.$refs.kioskEdit.show()
      },
      getRowCount (items) {
        return items.length
      },
      getBadge (status) {
        return formats.getBadge(status)
      },
      getYesNo (value) {
        return formats.format_yes_no(value)
      },
      isMobile () {
        if (screen.width <= 760) {
          return true
        } else {
          return false
        }
      },
      trimHeading (value) {
        if (value.length > 5 && this.isMobile()) {
          return value.substring(0, 8) + '..'
        }
        return value
      }
    },
    validations: {
      addKiosk: {
        name: {
          required
        }
      },
      editKiosk: {
        name: {
          required
        }
      }
    }
  }
</script>

<style scoped>

</style>



