<template>
  <div class="col-12">
    <b-card class="rounded" no-body bg-variant="dark" text-variant="dark">
      <b-tabs pills fill card>
        <!-- CONFIGURATION -->
        <b-tab title="Configuration" active class="p-0">
          <template slot="title">
            <i class="fa fa-cog mr-2"></i> Configuration
          </template>
          <AccountConfiguration/>
        </b-tab>
        <!-- USERS -->
        <b-tab title="Users" class="p-0">
          <template slot="title">
            <i class="fa fa-group mr-2"></i> Users
          </template>
          <AccountUsers/>
        </b-tab>
        <!-- DISABLE PAYMENT -->
        <!-- <b-tab title="Payment Methods" class="p-0">
          <template slot="title">
            <i class="fa fa-credit-card mr-2"></i> Payment Methods
          </template>
          <AccountCards/>
        </b-tab> -->
        <!-- DISBALE INVOICES -->
        <!-- <b-tab title="Invoices" class="p-0">
          <template slot="title">
            <i class="fa fa-file-pdf-o mr-2"></i> Payment Invoices
          </template>
          <AccountInvoices/>
        </b-tab> -->
        <!-- DISABLE INTEGRATIONS -->
        <!-- <b-tab title="Integration Links" class="p-0">
          <template slot="title">
            <i class="fa fa-server mr-2"></i> Integration Links
          </template>
          <AccountIntegrations/>
        </b-tab> -->
      </b-tabs>
    </b-card>
  </div>
</template>

<script>
import AccountUsers from '@/components/StorePages/Store/AccountUsers'
import AccountConfiguration from '@/components/StorePages/Store/AccountConfiguration'
import AccountCards from '@/components/StorePages/Store/AccountCards'
import AccountInvoices from '@/components/StorePages/Store/AccountInvoices'
import AccountIntegrations from '@/components/StorePages/Store/AccountIntegrations'

export default {
  name: 'full',
  components: {
    AccountUsers,
    AccountConfiguration,
    AccountCards,
    AccountInvoices,
    AccountIntegrations
  },
  data () {
    return {}
  },
  computed: {
    name () {
      return this.$route.name
    },
    list () {
      return this.$route.matched
    }
  }
}
</script>



