        <b-button type="submit" size="sm" variant="success" @click="updateData()" class="pull-right ml-1"><i class="fa fa-dot-circle-o"></i> Save</b-button>
        <a href="https://docs.range-pro.com/admin_account.html#configure-rangepro-settings" target="new" class="button">
          <b-button type="submit" size="sm" variant="primary" class="pull-right"><i class="fa fa-question-circle"></i> Help</b-button>
        </a>
      </div>
    </b-card>
</template>

<script>
  import rangepro from '@/services/rangepro'

  export default {
    name: 'AccountConfiguration',
    data () {
      return {
        result: {},
        loading: true
      }
    },
    mounted: function () {
      this.getData()
    },
    methods: {
      getData () {
        this.loading = true
        rangepro.get_secure('store/account', this.handleResponse, 'ErrorRegistrationAccount')
      },
      updateData () {
        this.loading = true
        rangepro.put_secure('store/account', this.result, this.getData, 'ErrorRegistrationAccount', 'Saved Account Information.')
      },
      handleResponse (data, success) {
        if (success) {
          this.result = data
          this.loading = false
        } else {
          this.loading = false
        }
      }
    }
  }
</script>

