<template>
  <div class="col-12">

    <b-card class="bg-white rounded mb-1" bg-variant="dark" text-variant="dark" no-body>
      <div slot="header" class="text-light">
        <strong>Store</strong> Ranges
        <router-link :to="{name: 'Store Range Configuration Add'}">
          <b-button id="btn801" type="submit" size="sm" variant="success" class="pull-right"><i class="fa fa-plus"></i> Add</b-button>
        </router-link>
        <a href="https://docs.range-pro.com/store_account.html#create-configure-range" target="new" class="button">
          <b-button id="btn802" type="submit" size="sm" variant="primary" class="pull-right mr-1"><i class="fa fa-question-circle"></i> Help</b-button>
        </a>
      </div>
    </b-card>


    <b-card v-if="loading" class="bg-white rounded text-center text-primary my-2" bg-variant="dark">
      <b-spinner class="align-middle"></b-spinner>
      <strong>Loading...</strong>
    </b-card>


    <b-card
      v-if="!loading"
      v-for="range in item_list"
      class="bg-white rounded mb-1"
      bg-variant="dark">
      <b-row class="p-3">
        <b-col cols="8" md="9" class="pl-3 pr-5">
          <b-row>
            <h2 class="text-gray-700"><i class="fa fa-bullseye mr-1 text-danger"></i> {{ trimHeading(range.name).toUpperCase() }}</h2>
            <medium class="text-primary text-sm-left mt-2 ml-2 mb-2">{{ range.lanes }} {{ range.terminology.toUpperCase() }}</medium>
          </b-row>
          <b-row>
            <b-col cols="8" md="3">
              <strong>Enabled:</strong>
            </b-col>
            <b-col cols="4" md="3">
              <b-badge pill class="ml-2 w-100" :variant="getBadge(range.enabled)">{{ getYesNo(range.enabled) }}</b-badge>
            </b-col>
            <b-col cols="8" md="3">
              <strong>Locked:</strong>
            </b-col>
            <b-col cols="4" md="3">
              <b-badge pill class="ml-2 w-100" :variant="getBadge(range.lock)">{{ getYesNo(range.lock) }}</b-badge>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="8" md="3">
              <strong>Auto-Assign:</strong>
            </b-col>
            <b-col cols="4" md="3">
              <b-badge pill class="ml-2 w-100" :variant="getBadge(range.auto_assignment)">{{ getYesNo(range.auto_assignment) }}</b-badge>
            </b-col>
            <b-col cols="8" md="3">
              <strong>Paused:</strong>
            </b-col>
            <b-col cols="4" md="3">
              <b-badge pill class="ml-2 w-100" :variant="getBadge(range.auto_assignment_paused)">{{ getYesNo(range.auto_assignment_paused) }}</b-badge>
            </b-col>
          </b-row>
        </b-col>
        <b-col cols="4" md="3">
          <router-link :to="{ name: 'Store Range Dashboard', params: { id:  range.id}}" class="text-decoration-none">
            <b-row class="mb-1">
                <b-button block id="btn803" type="submit" size="sm" variant="secondary"><i class="fa fa-th-large"></i> Dashboard</b-button>
            </b-row>
          </router-link>
          <router-link :to="{ name: 'Store Range Configuration Update', params: { id:  range.id}}" class="text-decoration-none">
            <b-row class="mb-1">
                <b-button block id="btn804" type="submit" size="sm" variant="warning"><i class="fa fa-trash-o"></i> Edit</b-button>
            </b-row>
          </router-link>
          <b-row class="mb-1">
            <b-button  block id="btn805" type="submit" size="sm" variant="danger" @click="confirmDelete(range.id)"><i class="fa fa-trash-o"></i> Delete</b-button>
          </b-row>
        </b-col>
      </b-row>
    </b-card>



    <b-modal ref="confirmDelete" hide-footer centered title="Delete Range" header-bg-variant="dark" header-text-variant="light">
      <div class="d-block text-left text-dark">
        <p>Please confirm to delete range.</p>
      </div>
      <b-btn id="btn351" class="mt-3" variant="outline-danger" block @click="deleteItem()">Confirm</b-btn>
    </b-modal>

  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import formats from '@/services/formats'

  export default {
    name: 'RangeConfiguration',
    data () {
      return {
        item_list: [],
        delete_id: null,
        loading: true
      }
    },
    mounted: function () {
      this.getData(null)
    },
    methods: {
      getData () {
        rangepro.get_secure('store/ranges', this.handleResponse, 'GetStoreRanges')
      },
      deleteItem () {
        this.loading = true
        this.$refs.confirmDelete.hide()
        rangepro.delete_secure('store/range', this.delete_id, this.getData, 'GetStoreRanges', 'Removed Store Range')
      },
      confirmDelete (id) {
        this.$refs.confirmDelete.show()
        this.delete_id = id
      },
      handleResponse (data, success) {
        if (success) {
          this.item_list = data.ranges
          this.loading = false
        } else {
          this.loading = false
        }
      },
      getRowCount (items) {
        return items.length
      },
      getBadge (status) {
        return formats.getBadge(status)
      },
      getYesNo (value) {
        return formats.format_yes_no(value)
      },
      isMobile () {
        if (screen.width <= 760) {
          return true
        } else {
          return false
        }
      },
      trimHeading (value) {
        if (value.length > 5 && this.isMobile()) {
          return value.substring(0, 8) + '..'
        }
        return value
      }
    }
  }
</script>

<style scoped>

</style>



