            key: 'active',
            label: 'Enabled',
            formatter: (value) => formats.format_yes_no(value)
          },
          {
            key: 'id',
            label: 'Action'
          }
        ],
        addMember: {
          first_name: null,
          last_name: null,
          phone: null,
          email: null
        },
        addConfiguration: {
          name: null,
          description: null,
          amount: null,
          max_participants: 1,
          interval: 'month',
          interval_count: 1
        },
        active_currentPage: 1,
        active_perPage: 25,
        active_totalRows: 0,
        other_currentPage: 1,
        other_perPage: 25,
        other_totalRows: 0,
        all_currentPage: 1,
        all_perPage: 25,
        all_totalRows: 0,
        configuration_currentPage: 1,
        configuration_perPage: 25,
        configuration_totalRows: 0,
        total_monthly: 0,
        total_yearly: 0,
        total_daily: 0,
        status: {
          active: 0,
          unpaid: 0,
          past_due: 0,
          canceled: 0
        },
        plans_active_members: {},
        membership_status_data: {
          labels: [],
          datasets: [
            {
              label: 'Membership Status',
              backgroundColor: '#1c6eb5',
              data: []
            }
          ]
        }
      }
    },
    mounted: function () {
      this.getConfiguration()
      this.getActiveMembers()
      this.getOtherMembers()
      this.getAllMembers()
      this.getMembershipStatusChart()
    },
    filters: {
      capitalize: function (value) {
        if (!value) return ''
        value = value.toString()
        return value.charAt(0).toUpperCase() + value.slice(1)
      }
    },
    methods: {
      getActiveMembers () {
        this.loading = true
        rangepro.get_secure('store/members/active', this.handleActiveMemberResponse, 'GetStoreActiveMembers')
      },
      getOtherMembers () {
        this.loading = true
        rangepro.get_secure('store/members/other', this.handleOtherMemberResponse, 'GetStoreOtherMembers')
      },
      getAllMembers () {
        this.loading = true
        rangepro.get_secure('store/members/all', this.handleAllMemberResponse, 'GetStoreAllMembers')
      },
      getMembershipStatusChart () {
        this.loading_summary = true
        rangepro.get_secure('store/memberships/summary', this.handleStatusChartResponse, 'GetMembershipStatus')
      },
      getConfiguration () {
        this.loading_configuration = true
        rangepro.get_secure('store/memberships', this.handleConfigurationResponse, 'ErrorRegistrationProductsGet')
      },
      refreshMemberships () {
        this.loading = true
        rangepro.delete_bulk_secure('store/members/refresh', {}, this.handleRefresh, 'GetRefreshMemberships', 'Memberships Updated')
      },
      createCustomer () {
        this.loading = true
        this.$refs.customerAdd.hide()
        var member = {
          member: this.addMember
        }
        rangepro.post_secure('store/members', member, this.handleCreateCustomerResponse, 'AddStoreMember', 'Successfully Added Customer')
      },
      createConfiguration () {
        this.loading_configuration = true
        this.$refs.addConfigurationModal.hide()
        var configuration = {
          product: {
            name: this.addConfiguration.name,
            description: this.addConfiguration.description,
            amount: parseFloat(this.addConfiguration.amount),
            interval: this.addConfiguration.interval,
            interval_count: this.addConfiguration.interval_count,
            max_participants: this.addConfiguration.max_participants
          }
        }
        rangepro.post_secure('store/memberships', configuration, this.getConfiguration, 'ErrorRegistrationProductsCreate', 'Added Membership Plan')
      },
      updateConfiguration (id, status) {
        this.loading_configuration = true
        var state = (status) ? 'deactivate' : 'activate'
        rangepro.put_secure('store/membership/' + id + '/' + state, {}, this.getConfiguration, 'ErrorRegistrationProductsGetUpdate', 'Changed Membership Status')
      },
      handleActiveMemberResponse (data, success) {
        if (success) {
          this.active_members = data.members
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleOtherMemberResponse (data, success) {
        if (success) {
          this.other_members = data.members
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleAllMemberResponse (data, success) {
        if (success) {
          this.all_members = data.members
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleCreateCustomerResponse (data, success) {
        if (success) {
          this.addMember.first_name = null
          this.addMember.last_name = null
          this.addMember.email = null
          this.addMember.phone = null
          router.push({name: 'Member Detail', params: {id: data.id}})
        }
        this.loading = false
      },
      handleConfigurationResponse (data, success) {
        if (success) {
          this.configuration = data.products
          this.loading_configuration = false
          this.addConfiguration.name = null
          this.addConfiguration.description = null
          this.addConfiguration.amount = null
          this.addConfiguration.max_participants = 1
          this.addConfiguration.interval = 'month'
          this.addConfiguration.interval_count = 1
        } else {
          this.loading_configuration = false
        }
      },
      handleRefresh () {
        var self = this
        setTimeout(function () { self.getActiveMembers() }, 2000)
        setTimeout(function () { self.getOtherMembers() }, 1000)
        setTimeout(function () { self.getAllMembers() }, 1000)
        setTimeout(function () { self.getMembershipStatusChart() }, 1000)
      },
      handleStatusChartResponse (data, success) {
        if (success) {
          this.status.active = data.summary['active']
          this.status.unpaid = data.summary['unpaid']
          this.status.past_due = data.summary['past_due']
          this.status.canceled = data.summary['canceled']
          this.plans_active_members = data.summary['plans']
          this.total_monthly = data.summary['totals']['per_month']
          this.total_daily = data.summary['totals']['per_day']
          this.total_yearly = data.summary['totals']['per_year']
          var tmpLabels = data.labels
          var tmpDS = {
            label: 'Membership Status',
            backgroundColor: this.getPieChartBackgrounds(data.data.length),
            data: data.data
          }
          this.membership_status_data = {
            labels: tmpLabels,
            datasets: [
              tmpDS
            ]
          }
          this.$refs.membershipStatusChart.updateChart()
          this.loading_summary = false
        } else {
          this.loading_summary = false
        }
      },
      clearPhone () {
        if (this.addMember.phone === '(###) ###-####') {
          this.addMember.phone = ''
        }
      },
      getRowCount (items) {
        return items.length
      },
      getBadge (status) {
        return formats.getBadge(status)
      },
      getPieChartBackgrounds (arrayLength) {
        var baseChoices = [
          'rgba(65, 184, 131, .8)',
          'rgba(228, 102, 81, .8)',
          'rgba(0, 216, 255, .8)',
          'rgba(155, 89, 182, .8)',
          'rgba(209, 169, 249, .8)',
          'rgba(169, 209, 249, .8)',
          'rgba(200, 13, 107, .8)',
          'rgba(200, 200, 13, .7)'
        ]
        return baseChoices.slice(0, arrayLength)
      },
      getActiveMemberCount (key) {
        for (var index in this.plans_active_members) {
          if (index === key) {
            return this.plans_active_members[index]
          }
        }
      }
    },
    validations: {
      addMember: {
        first_name: {
          required
        },
        last_name: {
          required
        },
        phone: {
          required
        },
        email: {
          required,
          email
        }
      },
      addConfiguration: {
        name: {
          required
        },
        description: {
          required
        },
        amount: {
          required
        },
        interval: {
          required
        },
        interval_count: {
          required
        },
        max_participants: {
          required
        }
      }
    }
  }
</script>

<style scoped>

</style>



