<template>
  <div class="col-12">
    <b-card class="bg-white rounded" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Store</strong> Shootdowns
        <router-link :to="{name: 'Store Shootdown Add'}">
          <b-button id="btn67" type="submit" size="sm" variant="success" class="pull-right">
            <i class="fa fa-plus"></i> Add
          </b-button>
        </router-link>
        <a href="https://docs.range-pro.com/store_account.html#shootdowns" target="new" class="button">
          <b-button id="btn68" type="submit" size="sm" variant="primary" class="pull-right mr-1"><i class="fa fa-question-circle"></i> Help</b-button>
        </a>
      </div>
      <div class="table-responsive">
        <b-table striped hover stacked="sm"
                 :busy="loading"
                 :current-page="currentPage"
                 :per-page="perPage"
                 :items="item_list"
                 :fields="fields">

          <div slot="table-busy" class="text-center text-primary my-2">
            <b-spinner class="align-middle"></b-spinner>
            <strong>Loading...</strong>
          </div>

          <template slot="id" slot-scope="data">
            <router-link :to="{ name: 'Store Shootdown Update', params: { id:  data.item.id}}">
              <b-button id="btn69" type="submit" size="sm" variant="warning"><i class="fa fa-trash-o"></i> Edit</b-button>
            </router-link>
            <b-button id="btn70" type="submit" size="sm" variant="danger" @click="confirmDelete(data.item.id)"><i class="fa fa-trash-o"></i> Delete</b-button>
          </template>
        </b-table>
      </div>

      <nav>
        <b-pagination :total-rows="getRowCount(item_list)" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
      </nav>

      <b-modal ref="confirmDelete" header-bg-variant="dark" header-text-variant="light" centered hide-footer title="Delete Shootdown">
        <div class="d-block text-left text-dark">
          <p>Please confirm to delete this shootdown.</p>
        </div>
        <b-btn id="btn334" class="mt-3" variant="outline-danger" block @click="deleteItem()">Confirm</b-btn>
      </b-modal>
    </b-card>

  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import formats from '@/services/formats'

  export default {
    name: 'ShootdownConfiguration',
    data () {
      return {
        item_list: [],
        delete_id: null,
        loading: true,
        currentPage: 1,
        perPage: 30,
        totalRows: 0,
        fields: [
          {
            key: 'name',
            label: 'Name',
            sortable: true
          },
          {
            key: 'status',
            label: 'Status'
          },
          {
            key: 'date',
            label: 'Date'
          },
          {
            key: 'time',
            label: 'Time'
          },
          {
            key: 'slots',
            label: 'Slots'
          },
          {
            key: 'id',
            label: 'Action'
          }
        ]
      }
    },
    mounted: function () {
      this.getData(null)
    },
    methods: {
      getData (response) {
        rangepro.get_secure('store/shootdowns', this.handleResponse, 'GetStoreShootdowns')
      },
      deleteItem () {
        this.loading = true
        this.$refs.confirmDelete.hide()
        rangepro.delete_secure('store/shootdown', this.delete_id, this.getData, 'DeleteStoreShootdown', 'Removed Shootdown')
      },
      confirmDelete (id) {
        this.$refs.confirmDelete.show()
        this.delete_id = id
      },
      handleResponse (data, success) {
        if (success) {
          this.item_list = data.shootdowns
          this.loading = false
        } else {
          this.loading = false
        }
      },
      getRowCount (items) {
        return items.length
      },
      getBadge (status) {
        return formats.getBadge(status)
      },
      formatDate (date) {
        return formats.format_date_std(date)
      }
    }
  }
</script>

<style scoped>

</style>



