<template>
  <b-card class="bg-white rounded mb-0" bg-variant="dark" text-variant="dark" style="border: none;">
    <div class="table-responsive">
      <b-table striped hover stacked="sm"
               :current-page="currentPage"
               :per-page="perPage"
               :items="result"
               :fields="fields">

        <template slot="paid" slot-scope="data">
          <b-badge :variant="getBadge(data.item.paid)">{{data.value}}</b-badge>
        </template>
        <template slot="attempted" slot-scope="data">
          <b-badge :variant="getBadge(data.item.attempted)">{{data.value}}</b-badge>
        </template>
        <template slot="id" slot-scope="data">
          <b-button id="btn13094785" type="submit" size="sm" variant="warning" @click="downloadInvoice(data.item.invoice_pdf)" v-if="data.item.paid"><i class="fa fa-file-pdf-o"></i> View Invoice</b-button>
        </template>
      </b-table>
    </div>

    <nav>
      <b-pagination :total-rows="getRowCount(result)" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
    </nav>

    <div slot="footer">
      <b-spinner v-if="loading" class="align-middle ml-2"></b-spinner>
      <a href="https://docs.range-pro.com/admin_account.html#invoices" target="new" class="button">
        <b-button type="submit" size="sm" variant="primary" class="pull-right"><i class="fa fa-question-circle"></i> Help</b-button>
      </a>
    </div>
  </b-card>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import formats from '@/services/formats'

  export default {
    name: 'RegistrationInvoices',
    data () {
      return {
        result: [],
        loading: true,
        currentPage: 1,
        perPage: 5,
        totalRows: 0,
        fields: [
          {
            key: 'date',
            label: 'Invoice Date',
            formatter: (value) => formats.format_stripe_ts(value),
            sortable: true
          },
          {
            key: 'amount_due',
            label: 'Amount Due',
            formatter: (value) => formats.format_stripe_amount(value)
          },
          {
            key: 'paid',
            label: 'Invoice Paid',
            formatter: (value) => formats.format_yes_no(value),
            sortable: true
          },
          {
            key: 'attempted',
            label: 'Attempted Charge',
            formatter: (value) => formats.format_yes_no(value),
            sortable: true
          },
          {
            key: 'attempt_count',
            label: 'Number of Attempts',
            sortable: true
          },
          {
            key: 'id',
            label: 'Action'
          }
        ]
      }
    },
    mounted: function () {
      this.getData()
    },
    methods: {
      getData () {
        rangepro.get_secure('store/invoices', this.handleResponse, 'ErrorRegistrationInvoices')
      },
      downloadInvoice (url) {
        window.open(url)
      },
      handleResponse (data, success) {
        if (success) {
          this.loading = false
          this.result = data.invoices
        } else {
          this.loading = false
        }
      },
      getRowCount (items) {
        return items.length
      },
      getBadge (status) {
        return formats.getBadge(status)
      }
    }
  }
</script>

<style scoped>

</style>



