                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="addPlayer.notifications">
          </b-form-select>
        </b-form-group>

        <b-btn id="btn333568693562348657" class="mt-3" variant="outline-success" block @click="savePlayer()" :disabled="$v.addPlayer.$invalid">Add Shooter</b-btn>

      </div>
    </b-modal>


    <b-modal ref="playerEdit" id="playerEdit" hide-footer size="lg" centered hide-footer title="Edit Shooter" header-bg-variant="dark" header-text-variant="light">
      <div ref="card" class="text-dark">

        <b-form-group
          label="Code"
          label-for="playerRef"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="playerRef" type="text" v-model="updatePlayer.playerRef" :disabled="true"></b-form-input>
        </b-form-group>

        <b-form-group
          label="First Name"
          label-for="playerFirstName"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="playerFirstName" type="text" v-model="updatePlayer.first_name"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.updatePlayer.first_name.required">First Name is Required</span>
        </b-form-group>

        <b-form-group
          label="Last Name"
          label-for="playerLastName"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="playerLastName" type="text" v-model="updatePlayer.last_name"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.updatePlayer.last_name.required">Last Name is Required</span>
        </b-form-group>

        <b-form-group
          label="Contact Number"
          label-for="playerPhone"
          :label-cols="2"
          :horizontal="true">
          <masked-input
            type="tel"
            name="phone"
            class="form-control"
            v-model="updatePlayer.phone"
            :mask="['(' , /[1-9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/]"
            :guide="true"
            placeholderChar="#" @focus="clearPhone()">
          </masked-input>
          <span class="form-group__message text-danger" v-if="!$v.updatePlayer.phone.required">Contact Number is Required</span>
        </b-form-group>

        <b-form-group
          label="Paid"
          label-for="playerPaid"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="playerPaid"
                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="updatePlayer.paid">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Notifications"
          label-for="playerNotifications"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="playerNotifications"
                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="updatePlayer.notifications">
          </b-form-select>
        </b-form-group>

        <b-btn id="btn456876978058476338" class="mt-3" variant="outline-success" block @click="editPlayer()" :disabled="$v.updatePlayer.$invalid">Update Shooter</b-btn>

      </div>
    </b-modal>


    <b-modal ref="playerScore" id="playerScore" size="lg" centered hide-footer title="Score Shooter" header-bg-variant="dark" header-text-variant="light">
      <div ref="card" class="text-dark">

        <b-form-group
          label="Code"
          label-for="playerRef"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="playerRef" type="text" v-model="updatePlayerScore.playerRef" :disabled="true"></b-form-input>
        </b-form-group>

        <b-form-group
          label="Total Score"
          label-for="playerScore"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="playerScore" type="text" v-model="updatePlayerScore.score"></b-form-input>
        </b-form-group>

        <b-form-group
          label="Total X's"
          label-for="playerXCount"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="playerXCount" type="text" v-model="updatePlayerScore.xscore"></b-form-input>
        </b-form-group>

        <b-btn id="btn456873453457876338" class="mt-3" variant="outline-success" block @click="updateScore()">Score Shooter</b-btn>

      </div>
    </b-modal>

  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import formats from '@/services/formats'
  import MaskedInput from 'vue-text-mask'
  import { required } from 'vuelidate/lib/validators'

  export default {
    name: 'ShootdownForm',
    components: {
      MaskedInput
    },
    data () {
      return {
        id: null,
        loading: false,
        hide: false,
        shootdown_configuration: {
          invalid_total_shooters: false,
          invalid_total_payout: false
        },
        shootdown: {
          name: null,
          status: 'registration',
          date: null,
          time: null,
          cost: 20,
          house_amt: 5,
          slots: 40,
          flights: []
        },
        current_flight: 1,
        flight: {},
        flight_payout: {
          first: 0,
          second: 0,
          third: 0
        },
        players: [],
        edit_id: null,
        delete_id: null,
        addPlayer: {
          first_name: null,
          last_name: null,
          phone: '#',
          paid: false,
          notifications: true,
          playerRef: this.getCode()
        },
        updatePlayer: {
          first_name: null,
          last_name: null,
          phone: '#',
          paid: false,
          notifications: true,
          playerRef: null
        },
        updatePlayerScore: {
          playerRef: null,
          score: null,
          xscore: null
        },
        fields: [
          {
            key: 'playerRef',
            label: 'Code',
            sortable: true,
            sortDirection: 'asc'
          },
          {
            key: 'last_name',
            label: 'Last Name',
            sortable: true,
            sortDirection: 'asc'
          },
          {
            key: 'first_name',
            label: 'First Name',
            sortable: true,
            sortDirection: 'asc'
          },
          {
            key: 'phone',
            label: 'Contact Number'
          },
          {
            key: 'paid',
            label: 'Paid',
            sortable: true,
            formatter: (value) => formats.format_yes_no(value)
          },
          {
            key: 'notifications',
            label: 'Notifications',
            formatter: (value) => formats.format_yes_no(value)
          },
          {
            key: 'id',
            label: 'Action'
          }
        ],
        scoring_fields: [
          {
            key: 'playerRef',
            label: 'Code',
            sortable: true,
            sortDirection: 'asc'
          },
          {
            key: 'last_name',
            label: 'Last Name',
            sortable: true,
            sortDirection: 'asc'
          },
          {
            key: 'first_name',
            label: 'First Name',
            sortable: true,
            sortDirection: 'asc'
          },
          {
            key: 'score',
            label: 'Total Score',
            sortable: true
          },
          {
            key: 'xscore',
            label: 'X Count',
            sortable: true
          },
          {
            key: 'id',
            label: 'Action'
          }
        ],
        shootdown_fields: [
          {
            key: 'position',
            label: 'Position',
            sortable: true,
            sortDirection: 'asc'
          },
          {
            key: 'last_name',
            label: 'Last Name'
          },
          {
            key: 'first_name',
            label: 'First Name'
          },
          {
            key: 'score',
            label: 'Total Score'
          },
          {
            key: 'xscore',
            label: 'X Count'
          },
          {
            key: 'round_defeated',
            label: 'Round Out'
          },
          {
            key: 'defeated',
            label: 'Defeated',
            formatter: (value) => formats.format_yes_no(value)
          },
          {
            key: 'id',
            label: 'Action'
          }
        ],
        filter: null,
        currentPage: 1,
        perPage: 10,
        totalRows: 0
      }
    },
    mounted: function () {
      if (this.$route.params.id) {
        this.loading = true
        this.id = this.$route.params.id
        this.getData(null)
        this.getPlayers(null)
      }
    },
    watch: {
      'shootdown': {
        handler: function (val, oldVal) {
          this.checkFlightConfiguration(val)
        },
        deep: true
      }
    },
    methods: {
      updateData () {
        this.loading = true
        this.getData()
        this.getPlayers()
      },
      getData (response) {
        this.loading = true
        rangepro.get_secure('store/shootdown/' + this.id, this.handleResponse, 'GetStoreShootdown')
      },
      getPlayers (response) {
        this.loading = true
        rangepro.get_secure('store/shootdown/' + this.id + '/players', this.handlePlayersResponse, 'GetStoreShootdownPlayers')
      },
      addItem () {
        this.loading = true
        var data = {
          shootdown: this.shootdown
        }
        data.shootdown.date = formats.format_date(this.shootdown.date)
        data.shootdown.time = formats.format_mil_to_std_time(this.shootdown.time)
        rangepro.post_secure('store/shootdowns', data, this.handleUpdateRepsonse, 'AddStoreShootdown', 'Saved Shootdown Configuration')
      },
      updateItem () {
        this.loading = true
        var data = {
          shootdown: this.shootdown
        }
        data.shootdown.date = formats.format_date(this.shootdown.date)
        data.shootdown.time = formats.format_mil_to_std_time(this.shootdown.time)
        rangepro.put_secure('store/shootdown/' + this.id, data, this.handleUpdateRepsonse, 'UpdateStoreShootdown', 'Updated Shootdown Configuration')
      },
      savePlayer () {
        this.loading = true
        var player = {
          player: this.addPlayer
        }
        rangepro.post_secure('store/shootdown/' + this.id + '/players', player, this.handleAddPlayerResponse, 'AddShootdownPlayer', 'Successfully Added Shooter')
      },
      editPlayer () {
        this.loading = true
        var player = {
          player: this.updatePlayer
        }
        rangepro.put_secure('store/shootdown/' + this.id + '/player/' + this.edit_id, player, this.handleAddPlayerResponse, 'EditShootdownPlayer', 'Successfully Edited Shooter')
      },
      updateScore () {
        this.loading = true
        var player = {
          player: {
            playerRef: parseInt(this.updatePlayerScore.playerRef),
            score: parseInt(this.updatePlayerScore.score),
            xscore: parseInt(this.updatePlayerScore.xscore)
          }
        }
        rangepro.put_secure('store/shootdown/' + this.id + '/player/' + this.edit_id, player, this.handleAddPlayerResponse, 'EditShootdownPlayer', 'Successfully Scored Shooter')
      },
      acceptScore (playerID) {
        this.loading = true
        var player = {
          player: {
            accepted_score: true
          }
        }
        rangepro.put_secure('store/shootdown/' + this.id + '/player/' + playerID, player, this.handleAddPlayerResponse, 'EditShootdownPlayer', 'Successfully Accepted Score')
      },
      deletePlayer () {
        this.loading = true
        this.$refs.confirmDelete.hide()
        rangepro.delete_secure('store/shootdown/' + this.id + '/player', this.delete_id, this.getPlayers, 'DeleteShotdownPlayer', 'Removed Shooter')
      },
      playerScoreLoss (id) {
        this.loading = true
        rangepro.put_secure('store/shootdown/' + this.id + '/player/' + id + '/loss', {}, this.handleMoveResponse, 'ScorePlayerLoss')
      },
      playerScoreUndo (id) {
        this.loading = true
        rangepro.put_secure('store/shootdown/' + this.id + '/player/' + id + '/undo', {}, this.handleMoveResponse, 'ScorePlayerUndo')
      },
      getFlight () {
        this.loading = true
        rangepro.get_secure('store/shootdown/' + this.id + '/flight/' + this.current_flight, this.handleFlightResponse, 'GetShootdownFlight')
      },
      movePlayerUp (id) {
        this.loading = true
        rangepro.put_secure('store/shootdown/' + this.id + '/player/' + id + '/up', {}, this.handleMoveResponse, 'MoveShootdownPlayer')
      },
      movePlayerDown (id) {
        this.loading = true
        rangepro.put_secure('store/shootdown/' + this.id + '/player/' + id + '/down', {}, this.handleMoveResponse, 'MoveShootdownPlayer')
      },
      routeUpdate () {
        if (this.id) {
          this.updateItem()
        } else {
          this.addItem()
        }
      },
      handleResponse (data, success) {
        if (success) {
          this.shootdown = data.shootdown
          this.shootdown.date = formats.get_date_for_input(data.shootdown['date'])
          this.shootdown.time = formats.format_std_time_to_mil(data.shootdown['time'])
          delete this.shootdown.id
          if (this.shootdown.status === 'shootdown') {
            this.getFlight()
          }
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleUpdateRepsonse (data, success) {
        if (success && this.id === null) {
          // get ID from success response and don't route back
          this.id = data.id
        }
        this.getData()
        this.getPlayers()
        this.loading = false
      },
      handlePlayersResponse (data, success) {
        if (success) {
          this.players = data.players
          this.loading = false
          this.checkFlightConfiguration(this.shootdown)
        } else {
          this.loading = false
        }
      },
      handleAddPlayerResponse (data, success) {
        if (success) {
          this.addPlayer.first_name = null
          this.addPlayer.last_name = null
          this.addPlayer.phone = null
          this.addPlayer.paid = false
          this.addPlayer.notifications = true
          this.addPlayer.playerRef = this.getCode()
          this.updatePlayer.first_name = null
          this.updatePlayer.last_name = null
          this.updatePlayer.phone = null
          this.updatePlayer.paid = false
          this.updatePlayer.notifications = true
          this.updatePlayer.playerRef = null
          this.updatePlayerScore.playerRef = null
          this.updatePlayerScore.score = null
          this.updatePlayerScore.xscore = null
          this.edit_id = null
          this.$refs.playerAdd.hide()
          this.$refs.playerEdit.hide()
          this.$refs.playerScore.hide()
          this.getPlayers()
        } else {
          this.loading = false
        }
      },
      handleFlightResponse (data, success) {
        if (success) {
          this.flight = data
          this.determineFlightPayout()
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleMoveResponse (data, success) {
        if (success) {
          this.getFlight()
          this.loading = false
        } else {
          this.loading = false
        }
      },
      selectPlayer (first, last, phone, paid, notifications, ref, id) {
        this.edit_id = id
        this.updatePlayer.first_name = first
        this.updatePlayer.last_name = last
        this.updatePlayer.phone = phone
        this.updatePlayer.paid = paid
        this.updatePlayer.notifications = notifications
        this.updatePlayer.playerRef = ref
        this.$refs.playerEdit.show()
      },
      scorePlayer (score, xscore, ref, id) {
        this.edit_id = id
        this.updatePlayerScore.playerRef = ref
        this.updatePlayerScore.score = score
        this.updatePlayerScore.xscore = xscore
        this.$refs.playerScore.show()
      },
      confirmDeletePlayer (id) {
        this.$refs.confirmDelete.show()
        this.delete_id = id
      },
      getBadge (status) {
        return formats.getBadge(status)
      },
      getRowCount (items) {
        return items.length
      },
      changeStatus (status) {
        this.shootdown.status = status
        this.updateItem()
      },
      changeFlight (flight) {
        this.current_flight = flight
        this.getFlight()
      },
      addFlight () {
        this.shootdown.flights.push({
          id: 0,
          shooters: 0,
          percent: 0,
          fp_percent: 50,
          sp_percent: 30,
          tp_percent: 20
        })
      },
      removeFlight (index) {
        this.shootdown.flights.splice(index, 1)
      },
      getCode () {
        return parseInt(Number(formats.get_ts()) / 1000 - 1000000000)
      },
      toggleHide () {
        if (this.hide) {
          this.hide = false
        } else {
          this.hide = true
        }
      },
      clearPhone () {
        if (this.addPlayer.phone === '(###) ###-####') {
          this.addPlayer.phone = ''
        }
        if (this.updatePlayer.phone === '(###) ###-####') {
          this.updatePlayer.phone = ''
        }
      },
      checkFlightConfiguration (val) {
        var totalShooters = 0
        var totalPayout = 0
        this.shootdown_configuration.invalid_total_shooters = false
        this.shootdown_configuration.invalid_total_payout = false
        for (var index in val.flights) {
          totalShooters += val.flights[index]['shooters']
          totalPayout += val.flights[index]['percent']
        }
        if (totalShooters !== this.players.length) {
          this.shootdown_configuration.invalid_total_shooters = true
        }
        if (totalPayout !== 100) {
          this.shootdown_configuration.invalid_total_payout = true
        }
      },
      determineFlightPayout () {
        var totalPot = (this.shootdown.cost - this.shootdown.house_amt) * this.players.length
        var flightPercent = 0
        var flightPot = 0
        var first = 0
        var second = 0
        var third = 0
        for (var index in this.shootdown.flights) {
          if (this.shootdown.flights[index]['id'] === this.current_flight) {
            flightPercent = this.shootdown.flights[index]['percent'] / 100
            first = this.shootdown.flights[index]['fp_percent'] / 100
            second = this.shootdown.flights[index]['sp_percent'] / 100
            third = this.shootdown.flights[index]['tp_percent'] / 100
          }
        }
        flightPot = totalPot * flightPercent
        this.flight_payout.first = flightPot * first
        this.flight_payout.second = flightPot * second
        this.flight_payout.third = flightPot * third
      }
    },
    validations: {
      shootdown: {
        name: {
          required
        },
        date: {
          required
        },
        time: {
          required
        },
        cost: {
          required
        },
        house_amt: {
          required
        },
        slots: {
          required
        }
      },
      addPlayer: {
        first_name: {
          required
        },
        last_name: {
          required
        },
        phone: {
          required
        }
      },
      updatePlayer: {
        first_name: {
          required
        },
        last_name: {
          required
        },
        phone: {
          required
        }
      }
    }
  }
</script>

<style scoped>

</style>



