<template>
  <b-card class="bg-white rounded mb-0" bg-variant="dark" text-variant="dark" style="border: none;">
    <div class="table-responsive">
      <b-table striped hover stacked="sm"
               :current-page="currentPage"
               :per-page="perPage"
               :items="result"
               :fields="fields">

        <template slot="id" slot-scope="data">
          <b-button type="submit" size="sm" variant="danger" @click="confirmDelete(data.value)"><i class="fa fa-trash-o"></i> Delete</b-button>
        </template>
      </b-table>
    </div>

    <nav>
      <b-pagination :total-rows="getRowCount(result)" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
    </nav>

    <b-modal ref="confirmDeleteCC" centered hide-footer title="Delete Payment Method" header-bg-variant="dark" header-text-variant="light">
      <div class="d-block text-left text-dark">
        <p>Please confirm to delete payment method.</p>
      </div>
      <b-btn class="mt-3" variant="outline-danger" block @click="deleteItem()">Confirm</b-btn>
    </b-modal>

    <b-modal ref="ccModal" id="ccModal" size="lg" centered header-bg-variant="dark" header-text-variant="light" hide-footer title="Add Payment Method" @shown="initStripe()">
      <div ref="creditCard" id="creditCard" class="text-dark p-2"></div>
      <b-btn id="btn30143256" class="mt-3" variant="outline-success" block @click="addStripeCard()">Add Card</b-btn>
    </b-modal>

    <div slot="footer">
      <b-spinner v-if="loading" class="align-middle ml-2"></b-spinner>
      <b-button type="submit" size="sm" variant="success" class="pull-right" v-b-modal.ccModal><i class="fa fa-plus"></i> Add</b-button>

      <a href="https://docs.range-pro.com/admin_account.html#create-an-rangepro-admin-account" target="new" class="button">
        <b-button type="submit" size="sm" variant="primary" class="pull-right mr-1"><i class="fa fa-question-circle"></i> Help</b-button>
      </a>
    </div>
  </b-card>
</template>

<script>
  import rangepro from '@/services/rangepro'

  export default {
    name: 'RegistrationPaymentMethods',
    data () {
      return {
        result: [],
        delete_id: null,
        loading: true,
        currentPage: 1,
        perPage: 5,
        totalRows: 0,
        card: null,
        stripe: null,
        fields: [
          {
            key: 'last4',
            label: 'Last 4 Digits',
            formatter: (value) => { return '***' + value }
          },
          {
            key: 'brand',
            label: 'Card Type'
          },
          {
            key: 'id',
            label: 'Action'
          }
        ]
      }
    },
    mounted: function () {
      this.getData(null)
    },
    methods: {
      getData (response) {
        rangepro.get_secure('store/cards', this.handleResponse, 'ErrorRegistrationCards')
      },
      deleteItem () {
        this.loading = true
        this.$refs.confirmDeleteCC.hide()
        rangepro.delete_secure('store/cards', this.delete_id, this.getData, 'ErrorRegistrationCardsDelete', 'Removed Payment Method')
      },
      confirmDelete (id) {
        this.$refs.confirmDeleteCC.show()
        this.delete_id = id
      },
      handleResponse (data, success) {
        if (success) {
          this.result = data.cards
          this.loading = false
        } else {
          this.loading = false
        }
      },
      getRowCount (items) {
        return items.length
      },
      initStripe () {
        this.stripe = Stripe(process.env.STRIPE_KEY)
        var elements = this.stripe.elements()
        this.card = elements.create('card')
        this.card.mount(this.$refs.creditCard)
      },
      addStripeCard () {
        this.loading = true
        this.$refs.ccModal.hide()
        this.stripe.createToken(this.card).then(result => {
          var data = {
            'card': {
              'token': result.token.id
            }
          }
          rangepro.post_secure('store/cards', data, this.getData, 'ErrorRegistrationCards', 'Added Payment Method')
        })
      }
    }
  }
</script>

<style scoped>

</style>



