<template>
    <b-card class="bg-white mb-0" bg-variant="dark" text-variant="dark" style="border: none;">
      <div class="table-responsive">
        <b-table striped hover stacked="sm"
                 :busy="loading"
                 :current-page="currentPage"
                 :per-page="perPage"
                 :items="result"
                 :fields="fields">

          <div slot="table-busy" class="text-center text-primary my-2">
            <b-spinner class="align-middle"></b-spinner>
            <strong>Loading...</strong>
          </div>

          <template slot="enabled" slot-scope="data">
            <b-badge :variant="getBadge(data.item.enabled)">{{data.value}}</b-badge>
          </template>
          <template slot="id" slot-scope="data">
            <b-button type="submit" size="sm" variant="warning" @click="updateItem(data.item.username, data.item.enabled)">
              <i class="fa fa-lock" v-if="data.item.enabled === true"></i>
              <i class="fa fa-unlock" v-if="data.item.enabled === false"></i>
              <span v-if="data.item.enabled === true">Disable</span>
              <span v-if="data.item.enabled === false">Enable</span>
            </b-button>
            <b-button type="submit" size="sm" variant="danger" @click="confirmDelete(data.item.username)"><i class="fa fa-trash-o"></i> Delete</b-button>
          </template>
        </b-table>
      </div>

      <nav>
        <b-pagination :total-rows="getRowCount(result)" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
      </nav>

      <b-modal ref="confirmDeleteUser" centered header-bg-variant="dark" header-text-variant="light" hide-footer title="Delete User">
        <div class="d-block text-left text-dark">
          <p>Please confirm to delete user.</p>
        </div>
        <b-btn class="mt-3" variant="outline-danger" block @click="deleteItem()">Confirm</b-btn>
      </b-modal>

      <b-modal ref="addModal" id="modal1" size="lg" centered hide-footer header-bg-variant="dark" header-text-variant="light" title="Add Store User">
        <div ref="card" class="text-dark">
          <b-form-group
            label="Username"
            label-for="username"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="username" type="text" v-model="username"></b-form-input>
            <span class="form-group__message text-danger" v-if="!$v.username.required">Username is Required</span>
          </b-form-group>

          <b-form-group
            label="First Name"
            label-for="first_name"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="first_name" type="text" v-model="first_name"></b-form-input>
            <span class="form-group__message text-danger" v-if="!$v.first_name.required">First Name is Required</span>
          </b-form-group>

          <b-form-group
            label="Last Name"
            label-for="last_name"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="last_name" type="text" v-model="last_name"></b-form-input>
            <span class="form-group__message text-danger" v-if="!$v.last_name.required">Last Name is Required</span>
          </b-form-group>

          <b-form-group
            label="Email Address"
            label-for="email"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="email" type="text" v-model="email"></b-form-input>
            <span class="form-group__message text-danger" v-if="!$v.email.required">Email Address is Required</span><span class="form-group__message text-danger" v-if="!$v.email.email">Must be valid Email Address</span>
          </b-form-group>

          <b-form-group
            label="Password"
            label-for="password"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="password" type="password" v-model="repeat_password"></b-form-input>
            <b-input-group-append class="pull-right">
              <b-btn variant="danger" v-if="score < 3">Too Weak</b-btn>
              <b-btn variant="success" v-if="score === 3">Perfect</b-btn>
            </b-input-group-append>
            <span class="form-group__message text-danger" v-if="!$v.repeat_password.required">Password is Required</span><span class="form-group__message text-danger" v-if="!$v.repeat_password.sameAsPassword">Passwords Must Be Identical.</span>
          </b-form-group>

          <b-form-group
            label="Repeat Password"
            label-for="repeat"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="repeat" type="password" v-model="tmp_password"></b-form-input>
            <span class="form-group__message text-danger" v-if="!$v.tmp_password.required">Repeat Password is Required</span> <span class="form-group__message text-danger" v-if="!$v.repeat_password.minLength">Passwords Must Be At Least 8 Characters.</span>
          </b-form-group>
        </div>
        <b-btn class="mt-3" variant="outline-success" block @click="addItem()" :disabled="$v.$invalid || score < 3">Add User</b-btn>
      </b-modal>

      <div slot="footer">
        <b-spinner v-if="loading" class="align-middle ml-2"></b-spinner>
        <b-button type="submit" size="sm" variant="success" class="pull-right" v-b-modal.modal1>
          <i class="fa fa-plus"></i> Add
        </b-button>
        <a href="https://docs.range-pro.com/admin_account.html#store-users" target="new" class="button">
          <b-button type="submit" size="sm" variant="primary" class="pull-right mr-1"><i class="fa fa-question-circle"></i> Help</b-button>
        </a>
      </div>
    </b-card>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import formats from '@/services/formats'
  import { required, email, sameAs, minLength } from 'vuelidate/lib/validators'

  export default {
    name: 'AccountUsers',
    data () {
      return {
        result: [],
        item_key: {
          'user': {
            'username': null,
            'first_name': null,
            'last_name': null,
            'email': null,
            'tmp_password': null
          }
        },
        item: {},
        username: null,
        first_name: null,
        last_name: null,
        email: null,
        score: 0,
        tmp_password: null,
        repeat_password: null,
        delete_id: null,
        loading: true,
        currentPage: 1,
        perPage: 5,
        totalRows: 0,
        card: null,
        stripe: null,
        fields: [
          {
            key: 'username',
            label: 'Username',
            sortable: true
          },
          {
            key: 'first_name',
            label: 'First Name',
            sortable: true
          },
          {
            key: 'last_name',
            label: 'Last Name',
            sortable: true
          },
          {
            key: 'email',
            label: 'Email Address'
          },
          {
            key: 'status',
            label: 'Account Status'
          },
          {
            key: 'enabled',
            label: 'Account Enabled',
            formatter: (value) => formats.format_yes_no(value)
          },
          {
            key: 'id',
            label: 'Action'
          }
        ]
      }
    },
    mounted: function () {
      this.clearItem()
      this.getData(null)
    },
    watch: {
      repeat_password: function (value) {
        this.score = 0
        var hasNumber = /\d/.test(value)
        var hasLowercase = /[a-z]/.test(value)
        var hasUppercase = /[A-Z]/.test(value)
        if (hasNumber) {
          this.score += 1
        }
        if (hasLowercase) {
          this.score += 1
        }
        if (hasUppercase) {
          this.score += 1
        }
      }
    },
    methods: {
      getData (response) {
        this.loading = true
        rangepro.get_secure('store/users', this.handleResponse, 'ErrorRegistrationUsers')
      },
      deleteItem () {
        this.loading = true
        this.$refs.confirmDeleteUser.hide()
        rangepro.delete_secure('store/user', this.delete_id, this.getData, 'ErrorRegistrationUsers', 'Removed Store User')
      },
      confirmDelete (id) {
        this.$refs.confirmDeleteUser.show()
        this.delete_id = id
      },
      addItem () {
        this.loading = true
        this.$refs.addModal.hide()
        this.item.user = {
          username: this.username,
          first_name: this.first_name,
          last_name: this.last_name,
          email: this.email,
          tmp_password: this.tmp_password
        }
        rangepro.post_secure('store/users', this.item, this.getData, 'ErrorRegistrationUsersCreate', 'Added Store User')
        this.clearItem()
      },
      updateItem (id, status) {
        this.loading = true
        var userStatus = {
          'user': {
            'enabled': status === false
          }
        }
        rangepro.put_secure('store/user/' + id, userStatus, this.getData, 'ErrorRegistrationUsers', 'Updated Store User')
      },
      clearItem () {
        this.item = this.item_key
        this.loading = false
      },
      handleResponse (data, success) {
        if (success) {
          this.result = data.users
          this.loading = false
        } else {
          this.loading = false
        }
      },
      getRowCount (items) {
        return items.length
      },
      getBadge (status) {
        return formats.getBadge(status)
      }
    },
    validations: {
      username: {
        required
      },
      first_name: {
        required
      },
      last_name: {
        required
      },
      email: {
        required,
        email
      },
      tmp_password: {
        required
      },
      repeat_password: {
        required,
        sameAsPassword: sameAs('tmp_password'),
        minLength: minLength(8)
      }
    }
  }
</script>

<style scoped>
  .modal-dialog {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
  }

  .modal-content {
    height: auto;
    min-height: 100%;
    border-radius: 0;
  }
</style>



