<template>
  <div class="col-12 p-3">
    <b-card class="bg-dark rounded text-light mb-0 mt-0">
      <h5><i class="fa fa-group mr-2"></i> MEMBER LOGIN AND REGISTRATION
        <a href="https://docs.range-pro.com/admin_account.html#integrations" target="new" class="button">
          <b-button type="submit" size="sm" variant="primary" class="pull-right"><i class="fa fa-question-circle"></i> Help</b-button>
        </a>
      </h5>
    </b-card>

    <b-card class="border-dark rounded mb-1 mt-1">
      <h6 v-if="dev"><i class="fa fa-external-link mr-2"></i>Member Registration Link: <a v-bind:href="devBase + memberBase + storeID" target="_new">{{ devBase }}{{ memberBase }}{{ storeID }}</a></h6>
      <h6 v-else><i class="fa fa-external-link mr-2"></i>Member Registration Link: <a v-bind:href="publicBase + memberBase + storeID" target="_new">{{ publicBase }}{{ memberBase }}{{ storeID }}</a></h6>
    </b-card>

    <b-card class="border-dark rounded mb-1 mt-1">
      <h6 v-if="dev"><i class="fa fa-external-link mr-2"></i>Member Login Link: <a v-bind:href="devBase + memberLoginBase" target="_new">{{ devBase }}{{ memberLoginBase }}</a></h6>
      <h6 v-else><i class="fa fa-external-link mr-2"></i>Member Login Link: <a v-bind:href="publicBase + memberLoginBase" target="_new">{{ publicBase }}{{ memberLoginBase }}</a></h6>
    </b-card>

    <b-card class="bg-dark rounded text-light mb-0 mt-4">
      <h5><i class="fa fa-calendar-check-o mr-2"></i> RESERVATIONS
        <a href="https://docs.range-pro.com/admin_account.html#integrations" target="new" class="button">
          <b-button type="submit" size="sm" variant="primary" class="pull-right"><i class="fa fa-question-circle"></i> Help</b-button>
        </a>
      </h5>
    </b-card>

    <b-card class="border-dark rounded mb-1 mt-1" v-for="item in ranges">
      <h6 v-if="dev">
        <h4><i class="fa fa-bullseye mr-2"></i>Range: {{ item.name }}</h4>
        <hr>
        <p><i class="fa fa-2x fa-facebook-square text-primary"></i> <i class="fa fa-2x fa-instagram text-danger"></i> <i class="fa fa-2x fa-warning text-warning ml-2"></i> <i>NOTE: Use this link for Facebook and Instagram Call-To-Action buttons so your customers can book reservations directly through Facebook or Instagram.</i></p>
        <p><i class="fa fa-external-link mr-2"></i> Link: <a v-bind:href="devBase + reservationBase + apiKey + '/range/' + item.id" target="_new">{{ devBase }}{{ reservationBase }}{{ apiKey }}/range/{{ item.id }}</a></p>
      </h6>
      <h6 v-else>
        <h4><i class="fa fa-bullseye mr-2"></i>Range: {{ item.name }}</h4>
        <hr>
        <p><i class="fa fa-2x fa-facebook-square text-primary"></i> <i class="fa fa-2x fa-instagram text-danger"></i> <i class="fa fa-2x fa-warning text-warning ml-2"></i> <i>NOTE: Use this link for Facebook and Instagram Call-To-Action buttons so your customers can book reservations directly through Facebook or Instagram.</i></p>
        <p><i class="fa fa-external-link mr-2"></i> Link: <a v-bind:href="publicBase + reservationBase + apiKey + '/range/' + item.id" target="_new">{{ publicBase }}{{ reservationBase }}{{ apiKey }}/range/{{ item.id }}</a></p>
      </h6>
    </b-card>

  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'

  export default {
    name: 'RegistrationInvoices',
    data () {
      return {
        storeID: null,
        apiKey: null,
        ranges: [],
        loading: true,
        stage: process.env.API_STAGE,
        dev: false,
        memberLoginBase: 'member.range-pro.com/#/login/',
        memberBase: 'member.range-pro.com/#/register/',
        reservationBase: 'reservations.range-pro.com/#/search/',
        devBase: 'https://dev-',
        publicBase: 'https://'
      }
    },
    mounted: function () {
      this.getData()
    },
    methods: {
      getData () {
        rangepro.get_secure('store/integrations', this.handleResponse, 'ErrorRegistrationInvoices')
      },
      handleResponse (data, success) {
        if (success) {
          this.loading = false
          this.storeID = data.integration['store_id']
          this.apiKey = data.integration['api_key']
          this.ranges = data.integration['ranges']
          if (this.stage === 'development' || this.stage === 'testing') {
            this.dev = true
          }
        } else {
          this.loading = false
        }
      }
    }
  }
</script>

<style scoped>

</style>



